import React from 'react'
import PropTypes from "prop-types"
import styled from 'styled-components'

// takes a string as prop
// appends one letter at a time to displayed string 
// blinking cursor infite - pink 

// start setinterval, cursor blinks 3 times
// stop setinterval, cursor stops blinking and stays visible 
// text begins appending
// text finishes appending
// cursor blinks 3 times
// cursor disappears?

class Typewriter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      printedText: '',
      visibleCursor: true,
      initialCursorCount: 0,
    }
    this.updateCursor = this.updateCursor.bind(this)
  }
  
  componentDidMount() {
    this.startShow()
  }

  updateCursor() {
    this.setState({visibleCursor: !this.state.visibleCursor, initialCursorCount: this.state.initialCursorCount+1}, this.startShow)
  }

  async startShow() {
    if(this.state.initialCursorCount < 6) {
      await this.fiveHundredMillisecondPromise()
      this.updateCursor()
    }
  }

  fiveHundredMillisecondPromise() {
    return new Promise(resolve => setTimeout(() => {resolve()}, 500)); 
  }

  render() {
    
    return (
      <Container>
        <span>{this.state.printedText}</span>
        <Cursor visibleCursor={this.state.visibleCursor}>|</Cursor>
      </Container>
    );
  }
}

const Container = styled.div`
      font-size: 2em;
      align-items: center;
      width: 100%;
    `
const Cursor = styled.span`
  color: 'lavendarblush';
  opacity = ${props => props.visibleCursor ? 100 : 0};
`

